.footnav {
	width: 100%;	
	background-position: center;
	background-size: cover;	
	position: fixed;
	bottom: 0;
	font-size: 0px;
	background-color: rgba(250,250,250,0.8);	
}
.footnav li {
	display: inline-block;
	width: 20%;
	text-align: center;
	letter-spacing: 1px;
	color: #666;
	font-size: 11px;
	padding-bottom: 2px;
}
.footnav li i {
	margin-left: 0;
}

