a:link, a:visited, a:hover, a:active {
    outline: none;
    color: #333333;
}
a {
    text-decoration: none;
    outline: none;
    -webkit-tap-highlight-color: transparent;
    color: #333;
}
* {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    -webkit-text-size-adjust: none;
}
.plist li {
    display: inline-block;
    width: 48%;
    background-color: #FFFFFF;
    padding: 10px;
    font-size: 12px;
    color: #999;
    position: relative;
    margin-left: 1.4%;
    margin-top: 1.4%;
}
img {
    border: 0;
    margin: auto;
    display: block;
}
.name {
    margin: 10px auto;
    color: #000;
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 99.9%;
}
