#content {padding-top:45px;}
.nav_bd {border:1px solid #ccc; border-radius:5px;margin-top:10px;}
.content_m {max-width:640px; padding:2% 2% 0 2%; margin:0 auto;}
.content_screen {padding-top:60px;}
.content_s {max-width:640px; margin:0 auto;}
.banner_box {margin-bottom:10px;}
.banner_box a {display:block; text-align:center;}
.banner_box .banner_info {border-bottom:1px solid #ccc; padding:10px 0; background:#fff;}
.nav {background:#fff; border-top:1px solid #ccc; border-bottom:1px solid #ccc;}
.navTip {padding:10px; border-bottom:1px solid #ccc;}
.nav_m li {float:left; width:33.3%; text-align:center; margin:10px 0;}
.nav_m li a {display:block;}
/* .n_new, .n_bag, .n_clothes, .n_sales, .n_shoes, .n_accessories {width:64px; height:64px; background:url(../images/nav.jpg) no-repeat; background-size:192px 128px; margin:0 auto;} */
.n_new {background-position:0 0;}
.n_bag {background-position:-64px 0;}
.n_clothes {background-position:-128px 0;}
.n_sales {background-position:0 -64px;}
.n_shoes {background-position:-64px -64px;}
.n_accessories {background-position:-128px -64px;}

.login_m {border:1px solid #ccc; border-radius:5px; box-shadow:0 0 5px #ccc;}
.l_box, .pasa {padding:15px 0; border-bottom:1px solid #ccc; height:4em;}
.l_box .l_box_l, .l_box .l_box_r {float:left;}
.l_box input {width:90%; height:20px; font-size:14px; border:none; color:#333;}
.l_box_l {width:30%; height:26px; text-align:center;}
.l_box_r {width:70%; height:26px;}
.heiAuto {height:auto;}
.hei70 {height:70px;}
.hei75 {height:75px;}
.hei50 {height:50px;}
.l_box_r input, .l_box_r textarea {padding-left:8px;}
.clear {clear:both;}


.lo_input_b {width:60%;}
.login_btn {text-align:center; padding:15px 0;}
.login_btn_n {width:100%; height:40px; margin:15px 0 10px 0; background:#ed0122; border-radius:3px; color:#fff; text-shadow:1px 1px 2px #333; font-size:14px;}
a.login_btn_n {display:block; width:100%; margin-top:15px; text-align:center; height:40px; background:#ed0122; border-radius:3px; color:#fff; text-shadow:1px 1px 2px #333; font-size:14px; line-height:2.8;}
a.login_btn_s {background:#ccc; border:#ccc; text-shadow:none;}
.login_m_n .login_btn {line-height:4; padding-bottom:40px;}
a.login_btn_ns {background:#ccc;}
#s {margin-right:5px;}
.phoneprice {font-size: 12px;margin-right: 4px;padding-left: 40%; padding-bottom:5px;color: #999999;} 

.b_log {padding:5px; text-align:right; height:20px;}
.b_log a {color:#ed0122;}




